import React, { useState } from "react"
import { useFlexSearch } from "react-use-flexsearch"
import { useDispatch } from "react-redux"
import SearchIcon from "@material-ui/icons/Search"
import ListItemText from "@material-ui/core/ListItemText";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import List from "@material-ui/core/List";
import Paper from "@material-ui/core/Paper"
import { Link } from "gatsby";
import InputBase from "@material-ui/core/InputBase"
import classNames from "classnames"
import { navigate } from "gatsby"
import Box from "@material-ui/core/Box";
import ListItem from "@material-ui/core/ListItem";
import ButtonGroup from "@material-ui/core/ButtonGroup"
import AniLink from "gatsby-plugin-transition-link/AniLink";
import SentimentVeryDissatisfied from "@material-ui/icons/SentimentVeryDissatisfied";
import Typography from "@material-ui/core/Typography";
import { guardarProductosBusquedaAction } from "../../state/reducers/productoReducers"
import Button from "@material-ui/core/Button"
import clsx from "clsx"
import { makeStyles } from "@material-ui/core/styles"
const useStyles = makeStyles(({ palette, ...theme }) => ({
  intro: {
    padding: "100px 0 0 !important",
    overflow: "visible !important",

    [theme.breakpoints.down("sm")]: {
      padding: "100px 0 0 !important",
    },
  },
  paperSearch: {
    backgroundColor: "white",
    paddingLeft: 5,
    display: "flex",
    alignItems: "center",
    textAlign: "center",
    width: "120%",
    height: 35,
    margin: 0,
    borderRadius: "5px",
  },
  inputShearch: {
    marginLeft: theme.spacing(1),
    flex: 1,
    border: "1px",
    whiteSpace: "normal",
    "&:focus": {
      outline: 0,
    },
  },
  resultadobusqueda: {
    position: "fixed",
    width: "25%",
    paddingLeft: "25%"
  },
  paperDescription: {
    height: 500,
    overflow: "auto",
  },
  estilosimagen: {
    maxWidth: "10%",
    marginLeft: 5,
    marginBottom: 0,
    marginTop: 0,
    padding: "0px",
    borderRadius: "100%"
  },
}))

const BuscadorGeneral = ({ index, store, segmento }) => {
  const estilos = useStyles()
  const dispatch = useDispatch()
  const [searchQuery, setSearchQuery] = useState("")
  const results = useFlexSearch(searchQuery, index, store)
  const hasNoResults = searchQuery.length > 0 && results.length === 0

  const MostrarBusquedaCatalogo = e => {
    if (e.keyCode === 13 || e.keyCode === 9) {
      e.preventDefault()
      dispatch(guardarProductosBusquedaAction(results))
      navigate(`/buscador/`)
    }
  }
  const BusquedaCatalogoClick = e => {
    e.preventDefault()
    navigate(`/buscador/`)
    dispatch(guardarProductosBusquedaAction(results))
  }

  return (
    <>
      <ButtonGroup
        color="primary"
        aria-label="split button"
        style={{ width: "120%" }}
      >
        <Paper component="form" className={classNames(estilos.paperSearch)}>
          <InputBase
            className={classNames(estilos.inputShearch)}
            value={searchQuery}
            onChange={e => setSearchQuery(e.target.value)}
            placeholder="Busqueda de Productos"
            onKeyDown={MostrarBusquedaCatalogo}
          />
        </Paper>
        <Button
          className={clsx("bg-blue  text-13 text-white ")}
          aria-label="select merge strategy"
          aria-haspopup="menu"
          onClick={BusquedaCatalogoClick}
        >
          <SearchIcon style={{ color: "white" }} />
        </Button>
      </ButtonGroup>
      {results.length > 0 && (
        <div className={classNames(estilos.resultadobusqueda)}>
          <div style={{ position: "absolute", paddingTop: "2%" }}>
            <Paper className={classNames(estilos.paperDescription)}>
              <List>
                {results.map((item) => {
                  return (
                    <ListItem button>
                      <Link
                        to={`/producto/${item.ItemCodeAux}/`}
                        style={{ textDecoration: "none" }}
                      >
                        <Grid
                          container
                          alignItems="center"
                          justify="center"
                        >

                          <img
                            className={classNames(estilos.estilosimagen)}
                            src={
                              "https://disdelsa.com/imagenes/productos/" +
                              item.Imagen
                            }
                          />
                          <ListItemText
                            primary={
                              <Box style={{ fontSize: 10, textDecoration: "none", color: "black" }}>
                                {item.Descripcion}
                              </Box>
                            }
                            className={classNames(estilos.estilotexto)}
                          />
                        </Grid>
                        <Divider />
                      </Link>
                    </ListItem>
                  );
                })}
              </List>
            </Paper>

          </div>
        </div>
      )}
      {hasNoResults && (
        <div className={classNames(estilos.resultadobusqueda)}>
          <div style={{ position: "absolute", paddingTop: "2%" }}>
            <Paper
              className={classNames(
                estilos.paperDescription
              )}
            >
              <div container>
                <div>
                  <div className={classNames(estilos.busqueda)}>
                    <SentimentVeryDissatisfied
                      fontSize="large"
                      style={{ fontSize: 40 }}
                    />
                    <Typography variant="h6" style={{ margin: "10px" }}>
                      Lo sentimos!
                    </Typography>
                  </div>
                  <Typography style={{ margin: "5px" }}>
                    no se encontraron coincidencias de busqueda.
                  </Typography>
                  <br />

                  <strong>
                    Quiz√°s puedan interesarte las siguientes secciones
                  </strong>
                  <Grid container>
                    <Grid item md={12} xs={12}>
                      <Grid container>
                        {segmento.map(
                          (item, index) => (
                            <Grid
                              item
                              md={4}
                              xs={3}
                              key={index.toString()}
                              style={{ margin: "0px" }}
                            >
                              <AniLink
                                paintDrip
                                hex="#93292E"
                                to={`/${item.SegmentoAux}/`}
                                entryOffset={80}
                                duration={0.3}
                              >
                                <img
                                  src={`https://disdelsa.com/imagenes/${item.Imagen}`}
                                  alt={item.Imagen}
                                  style={{ padding: 15, width: 125 }}
                                />
                              </AniLink>
                              <Typography
                                style={{
                                  fontFamily: "Lobster",
                                  textAlign: "center",
                                }}
                              >
                                <Box fontWeight="fontWeightBold">
                                  {item.NombreSegmento}
                                </Box>
                              </Typography>
                            </Grid>
                          )
                        )}
                      </Grid>
                    </Grid>
                  </Grid>
                </div>
              </div>
            </Paper>
          </div>
        </div>
      )}
    </>
  )
}

export default BuscadorGeneral
